import React from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router';

import { getTracks } from '../actions/tracks';
import Menu from '../containers/Menu';

const App = ({ tracks, onAddTrack, onFindTrack, onGetTracks, ownProps }) => {
  let trackInput = '';
  let searchInput = '';

  const addTrack = () => {
      onAddTrack(trackInput.value);
      trackInput.value = '';
  }

  const findTrack = () => {
    console.log('findTrack', searchInput.value);
    onFindTrack(searchInput.value);
  }

  return (
    <div>
      <Menu/>
      <div>
        <input type="text" ref={(input) => { trackInput = input }} />
        <button onClick={addTrack}> </button>
      </div>
      <div>
        <input type="text" ref={(input) => { searchInput = input }} />
        <button onClick={findTrack}> </button>
      </div>
      <div>
        <button onClick={onGetTracks}>  (,  2 .)</button>
      </div>
      <ul>
        {tracks.map((track, index) =>
          <li key={index}>
            <Link to={`/tracks/${track.id}`}>{track.name}</Link>
          </li>
        )}
      </ul>
    </div>
  );
}

export default connect(
  (state, ownProps) => ({
    tracks: state.tracks.filter(track => track.name.includes(state.filterTracks)),
    ownProps
  }),
  dispatch => ({
    onAddTrack: (name) => {
      const payload = {
        id: Date.now().toString(),
        name
      };
      dispatch({ type: 'ADD_TRACK', payload });
    },
    onFindTrack: (name) => {
      console.log('name', name);
      dispatch({ type: 'FIND_TRACK', payload: name});
    },
    onGetTracks: () => {
      dispatch(getTracks());
    }
  })
)(App);



